export function typeChange(event) {
  selectedType = event.target.value;
  if (selectedType == "") {
    document.getElementById("type-error").textContent = "Please select a type";
    setEmptyList();
    setDisableFilter(true);
    hideFilter();
  } else {
    pageNo = 1;
    showFilter();
    document.getElementById("filter").value = "";
    document.getElementById("loader").style.display = "grid";
    document.getElementById("type-error").textContent = "";
    makeApiCall(cmsSerachApi, "type=" + selectedType + "&pageNo=" + pageNo).then((data) => {
      clearList();
      if (data.error) {
        setEmptyList();
      } else {
        setList(data);
        setDisableFilter(false);
      }
    });
    // console.log(event.target.value)
  }
}

function setListHtml() {
  document.getElementById("list").innerHTML = "";
  document.getElementById("loader").style.display = "none";
  listToDisplay.map((item) => {
    var itemdiv = document.createElement("div");
    itemdiv.className = "itemDiv";

    var itemContent = document.createElement("div");
    itemContent.className = "item";
    itemContent.id = item.id;
    itemContent.innerText = item.name;

    var itemBtn = document.createElement("button");
    itemBtn.id = item.id;
    itemBtn.innerText = "+";
    itemBtn.setAttribute("data-parameter", item.name);
    itemBtn.title = "Add Parameter";
    itemBtn.className = "addItem-btn ";
    itemdiv.appendChild(itemContent);
    if (selectedType == "Parameter") {
      itemBtn.addEventListener("click", addParameter);
      itemdiv.appendChild(itemBtn);
    }else{
      itemContent.addEventListener("click", itemClick);
      itemBtn.addEventListener("click", addItem);
      itemdiv.appendChild(itemBtn);
    }

    
    document.getElementById("list").appendChild(itemdiv);
    // console.log(item);
  });

  if (remainingItem > 0) {
    document.getElementById("loadBtn").style.display = "block";
    document.getElementById("loadBtn").addEventListener("click", loadMore);
  } else {
    document.getElementById("loadBtn").style.display = "none";
  }
}

function clearList() {
  listToDisplay = [];
}

function setEmptyList() {
  clearList();
  document.getElementById("loadBtn").style.display = "none";
  document.getElementById("list").innerHTML = "";
  document.getElementById("loader").style.display = "none";
  // const noItem = document.createElement('div')
  // noItem.id = "noitems"
  // noItem.innerText = "No items available...!"
  // document.getElementById('list').appendChild(noItem)
}

function setList(data) {
  if (!data.error) {
    if (data.values.length > 0) {
      remainingItem = data.totalCount - 20 * pageNo;
      // listToDisplay.
      data.values.map((item) => listToDisplay.push(item));
      setListHtml();
    } else {
      setEmptyList();
    }
  }
}

async function makeApiCall(url, queury) {
  var response = await fetch(url + queury, {
    mode: "cors",
    method: "GET",
    headers: {
      "appian-api-key": api_key,
      "content-type": "application/json;charset=ISO-8859-1",
    },
    referrerPolicy: "origin-when-cross-origin",
  });
  var data = await response.json();
  return data;
}

function setItemContent(data) {
  document.getElementById("itemName").innerText = data.name;
  document.getElementById("itemDetails").innerHTML = data.content;
  document.getElementById("backButton").addEventListener("click", back);
  document.getElementById("addButton").addEventListener("click", insertContent);
}

export function loadMore(event) {
  document.getElementById("loadBtn").style.display = "none";
  document.getElementById("loadingbtn").style.display = "flex";
  pageNo += 1;
  makeApiCall(cmsSerachApi, "type=" + selectedType + "&pageNo=" + pageNo + "&filter=" + filter)
    .then((data) => {
      // console.log(data);
      setList(data);
      document.getElementById("loadBtn").style.display = "block";
      document.getElementById("loadingbtn").style.display = "none";
    })
    .catch((err) => console.log(err));
}

function itemClick(event) {
  document.getElementById("itemsBody").style.display = "none";
  document.getElementById("loader").style.display = "grid";
  var id = event.target.id;
  makeApiCall(cmsSerachApi, "id=" + id + "&type=" + selectedType).then((result) => {
    setItemContent(result);
    document.getElementById("clauseContents").style.display = "none";
    document.getElementById("loader").style.display = "none";
    document.getElementById("itemContent").style.display = "block";
  });
}

