export function back() {
  document.getElementById("clauseContents").style.display = "block";
  document.getElementById("itemsBody").style.display = "block";
  document.getElementById("itemContent").style.display = "none";
}

export function inputStarted() {
  document.getElementById("loader").style.display = "grid";
  document.getElementById("itemsBody").style.display = "none";
  clearTimeout(typingTimer);
  typingTimer = setTimeout(doneTyping, doneTypingInterval);
}

export function inputEnded() {
  clearTimeout(typingTimer);
}

function doneTyping() {
  filter = document.getElementById("filter").value;
  pageNo = 1;
  listToDisplay = [];
  // console.log(dummy);
  makeApiCall(cmsSerachApi, "type=" + selectedType + "&pageNo=" + pageNo + "&filter=" + filter).then((data) => {
    // console.log(data);
    setList(data);
    document.getElementById("itemsBody").style.display = "block";
  });
}

function setDisableFilter(val) {
  document.getElementById("filter").disabled = val;
}

function showFilter() {
  document.getElementById("filterSelected").style.display = "block";
  document.getElementById("filterSelected").addEventListener("keyup", inputStarted);
  document.getElementById("filterSelected").addEventListener("keydown", inputEnded);
}

function hideFilter() {
  document.getElementById("filterSelected").style.display = "none";
}

export async function insertTextIntoDocument(text) {
  return Word.run(async (context) => {
    const paragraph = context.document.getSelection().insertText(text, Word.InsertLocation.after);
    await context.sync();
  });
}
